<template>
    <div class="data-editor-password">
        <navigate :title="navigateTitle" position="absolute"/>
        <div class="scroll-wrapper">
            <mu-form class="form" :model="form" label-position="left" label-width="100" ref="form">
                <mu-form-item class="form-item-s border-1px-b" prop="oldPwd" label="原密码" :rules="passwordRules1">
                    <mu-text-field v-model="form.oldPwd" placeholder="请填写原密码"
                                   :action-icon="visibility1 ? 'visibility' : 'visibility_off'"
                                   :action-click="() => {this.visibility1 = !this.visibility1}"
                                   :type="visibility1 ? 'text' : 'password'"
                                   solo full-width/>
                </mu-form-item>
                <mu-form-item class="form-item-s border-1px-b" prop="newPwd" label="新密码" :rules="passwordRules2">
                    <mu-text-field v-model="form.newPwd" placeholder="6-20位字母、数字和符号"
                                   :action-icon="visibility2 ? 'visibility' : 'visibility_off'"
                                   :action-click="() => {this.visibility2 = !this.visibility2}"
                                   :type="visibility2 ? 'text' : 'password'"
                                   solo full-width/>
                </mu-form-item>
                <mu-form-item class="form-item-s border-1px-b" prop="confirmPwd" label="确认新密码" :rules="passwordRules3">
                    <mu-text-field v-model="form.confirmPwd" placeholder="6-20位字母、数字和符号"
                                   :action-icon="visibility3 ? 'visibility' : 'visibility_off'"
                                   :action-click="() => {this.visibility3 = !this.visibility3}"
                                   :type="visibility3 ? 'text' : 'password'"
                                   solo full-width/>
                </mu-form-item>
            </mu-form>
            <div class="button-wrapper">
                <mu-button class="button" color="#fff" @click="submit" v-loading="loading"
                           data-mu-loading-size="24"
                           :disabled="loading"
                           flat>提交
                </mu-button>
            </div>
        </div>
    </div>
</template>

<script>
    import Navigate from "../../../base/navigate/navigate"
    import {updateUserPassword} from "../../../api/personal"
    import {userMixins} from "../../../common/mixins"

    export default {
        name: 'reset-password',
        mixins: [userMixins],
        data() {
            return {
                loading: false,
                visibility1: false,
                visibility2: false,
                visibility3: false,
                /***
                 * 验证
                 */
                passwordRules1: [
                    {validate: val => !!val, message: '必须填写原密码'},
                    {validate: val => /^[a-zA-Z\d]{6,16}$/.test(val), message: '请输入6-16位密码，不支持特殊符号'}
                ],
                passwordRules2: [
                    {validate: val => !!val, message: '必须填写新密码'},
                    {validate: val => /^[a-zA-Z\d]{6,16}$/.test(val), message: '请输入6-16位密码，不支持特殊符号'}
                ],
                passwordRules3: [
                    {validate: val => !!val, message: '必须填写确认新密码'},
                    {validate: val => /^[a-zA-Z\d]{6,16}$/.test(val), message: '请输入6-16位密码，不支持特殊符号'},
                    {validate: val => val === this.form.newPwd, message: '两次输入密码不一致'}
                ],
                /***
                 * 表单内容
                 *      oldPwd: 旧密码
                 *      newPwd: 新密码
                 */
                form: {
                    oldPwd: '',
                    newPwd: '',
                    confirmPwd: ''
                }
            }
        },
        computed: {
            /***
             * 导航栏标题
             * @return {string}
             */
            navigateTitle() {
                return '修改密码'
            }
        },
        activated() {
            this.$refs.form.clear()

            this.form = {
                oldPwd: '',
                newPwd: '',
                confirmPwd: ''
            }
        },
        methods: {
            /***
             * 提交 修改密码
             */
            submit() {
                this.$refs.form.validate().then(result => {
                    if (result) {
                        this.loading = true
                        updateUserPassword(this.form).then(res => {
                            this.loading = false

                            this.$toast('您的密码已更改，请重新登录')

                            this.logout()

                            this.$router.replace('/login')
                        }).catch(err => {
                            console.log(err)
                            this.loading = false
                        })
                    }
                })
            }
        },
        components: {
            Navigate
        }
    }
</script>

<style lang="stylus" scoped>
    .data-editor-password
        padding-top 46px
        background-color #fff
        .scroll-wrapper
            width 100%
            height 100%
            .form .form-item-s
                margin 0
                padding 15px

    .button-wrapper
        padding 20px
        .button
            width 100%
            background-color #2196f3

</style>



